
import { computed, ComputedRef, getCurrentInstance, ref } from 'vue';
import { BAR_MAP } from './utils';
import { BarMap, BarMapItem } from './index.d';

export default {
  name: 'c-scrollbar-bar',
  props: {
    direction: {
      type: String,
      default: 'vertical', // 2  horizontal  vertical
    },
    size: {
      // thumb
      type: String,
    },
    move: {
      type: String,
    },
    trigger: {
      type: String,
      default: 'hover', // hover  always  none 
    },
    barStyle: {
      // 
      type: Object,
      default: () => ({
        'background-color': '',
      }),
    },
    thumbStyle: {
      // 
      type: Object,
      default: () => ({
        'background-color': 'rgba(0, 0, 0, 0.2)',
      }),
    },
  },
  setup(props: any) {
    const bar: ComputedRef<BarMapItem> = computed(() => (BAR_MAP as BarMap)[props.direction]);
    const thumbStyleObj = computed(() => ({
      ...props.thumbStyle,
      [bar.value.size]: props.size,
      transform: `translate${bar.value.axis}(${props.move})`,
    }));

    const instance: any = getCurrentInstance();

    /**
     *   
     */
    const thumbRef = ref();
    function handleBarMouseDown(e: any) {
      const client = e[bar.value.client]; // 
      const wrap = instance.ctx.$el.getBoundingClientRect()[bar.value.direction]; // 
      const offset = Math.abs(wrap - client); // 
      const thumbHalf = thumbRef.value[bar.value.offset] / 2; // thumb
      const $parentWrap = instance.parent.ctx.wrap;
      const thumbPosPercent = ((offset - thumbHalf) * 100) / instance.ctx.$el[bar.value.offset];
      $parentWrap[bar.value.scroll] = (thumbPosPercent * $parentWrap[bar.value.scrollSize]) / 100;
    }

    const thumbDrag = ref(false);

    /**
     * 
     */
    function handleThumbMove(e: any) {
      if (!thumbDrag.value) {
        return;
      }
      const client = e[bar.value.client]; // 
      const wrap = instance.ctx.$el.getBoundingClientRect()[bar.value.direction]; // 
      const offset = Math.abs(wrap - client); // 
      const thumbHalf = thumbRef.value[bar.value.offset] / 2; // thumb
      const $parentWrap = instance.parent.ctx.wrap;
      const thumbPosPercent = ((offset - thumbHalf) * 100) / instance.ctx.$el[bar.value.offset];
      $parentWrap[bar.value.scroll] = (thumbPosPercent * $parentWrap[bar.value.scrollSize]) / 100;
    }

    /**
     * 
     */
    function hanldeThumbUp(event: any) {
      thumbDrag.value = false;
      if (event.target) {
        document.removeEventListener('mousemove', handleThumbMove); // documentevent.target
        document.removeEventListener('mouseup', hanldeThumbUp);
      }
      document.onselectstart = () => null;
    }

    /**
     * 
     */
    function startDrag(event: Event) {
      thumbDrag.value = true;
      if (event.target) {
        document.addEventListener('mousemove', handleThumbMove);
        document.addEventListener('mouseup', hanldeThumbUp);
      }
      document.onselectstart = () => false;
    }

    /**
     * thumb
     */
    function handleThumbMouseDown(event: Event) {
      startDrag(event);
    }

    return {
      thumbRef,
      bar,
      thumbStyleObj,
      handleBarMouseDown,
      handleThumbMouseDown,
    };
  },
};
